
* {
  margin: 0;
  padding: 0;
}
html,
body,
#app {
  height: 100%;
}
iframe {
  width: 100%;
  height: 100%;
  border: none;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#app #main {
    position: relative;
    height: 100%;
}
#app #main .router-view {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      overflow: auto;
}
li {
  list-style-type: none;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: #5e5e5e;
}



