
.vue-carousel-3d {
  position: relative;
}
.vue-carousel-3d.tansition-off .img-item {
    -webkit-transition: none !important;
    transition: none !important;
    left: 50% !important;
    right: auto;
    -webkit-transform: translateX(-50%) scale(0.8) !important;
            transform: translateX(-50%) scale(0.8) !important;
}
.vue-carousel-3d .imgs-container {
    width: 100%;
    height: 100%;
}
.vue-carousel-3d .imgs-container > .img-item {
      position: absolute;
}
.vue-carousel-3d .imgs-container > .img-item img {
        width: 100%;
}
.vue-carousel-3d .imgs-container.to-next .prev {
      z-index: 2;
}
.vue-carousel-3d .imgs-container.to-next .next {
      z-index: 1;
}
.vue-carousel-3d .imgs-container .img-item {
      left: 50%;
      -webkit-transform: translateX(-50%) scale(0.8);
              transform: translateX(-50%) scale(0.8);
      -webkit-transform-origin: center;
              transform-origin: center;
      -webkit-transition: left .4s, right .4s, -webkit-transform .4s;
      transition: left .4s, right .4s, -webkit-transform .4s;
      transition: transform .4s, left .4s, right .4s;
      transition: transform .4s, left .4s, right .4s, -webkit-transform .4s;
}
.vue-carousel-3d .imgs-container .prev,
    .vue-carousel-3d .imgs-container .next {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
}
.vue-carousel-3d .imgs-container .prev {
      -webkit-transform-origin: left;
              transform-origin: left;
      left: 0;
      z-index: 1;
}
.vue-carousel-3d .imgs-container .next {
      left: auto;
      right: 0;
      z-index: 2;
      -webkit-transform-origin: right;
              transform-origin: right;
}
.vue-carousel-3d .imgs-container .active {
      left: 50%;
      z-index: 3;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
.vue-carousel-3d .dots {
    position: absolute;
    z-index: 4;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.vue-carousel-3d .dots .dot {
      display: inline-block;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      width: 16px;
      height: 8px;
      margin: 4px;
      border: 2px solid transparent;
      background: rgba(0, 0, 0, 0.6);
      cursor: pointer;
}
.vue-carousel-3d .dots .dot.active-dot {
        background: #fff;
}



