
.vue-msg-demo button {
  cursor: pointer;
  display: block;
  border: none;
  line-height: 40px;
  font-size: 18px;
  margin: 20px auto;
  padding: 0 20px;
  border-radius: 8px;
  outline: none;
  color: #fff;
}
.vue-msg-demo button.succ {
    background: #13ce66;
}
.vue-msg-demo button.err {
    background: red;
}
.vue-msg-demo button.warn {
    background: orange;
}
.vue-msg-demo button.msg {
    background: #20a0ff;
}
.vue-msg-demo button.random {
    background: #000;
}



