
.demo-header {
  text-align: center;
  margin: 18px;
}
.demo-header .demo-logo {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    display: block;
    background-size: contain;
}
.demo-header > * {
    display: inline-block;
}
.demo-header .links {
    margin-left: 10px;
}
.demo-header .links .link {
      background: #ccc;
      padding: 0 10px;
      margin: 0 2px;
      border-radius: 15px;
      line-height: 22px;
      display: inline-block;
      background: #13ce66;
      color: #fff;
}
.demo-header .links .link:hover {
        text-decoration: underline;
}
.demo-header .links .unfinished {
      background: #eee;
}



